<template>

<div class="card border-success mb-3" style="max-width: 30rem;">
  <div class="card-header">{{state.goalDTO.name}}</div>
  <div class="card-body text-success">
    <h5 class="card-title ellipsis">{{state.goalDTO.content}}</h5>
    <p class="card-text text-right">{{aNewsEntry.qdone}}/{{aNewsEntry.qgoal}}{{aNewsEntry.unit}}</p>
    <!-- <h7 class="card-text">FOR DEV goalid: {{aNewsEntry.goalid}}</h7> -->
  </div>
</div>

<!-- <main class="card">
  
  <section >
    <h3>Task name:{{state.goalDTO.name}}</h3>
    not accurate but works...
    <div class="low-line-gap">
    <p>Details : {{state.goalDTO.content}}</p>
    <p class="align-right">status: {{aNewsEntry.qdone}}/{{aNewsEntry.qgoal}}</p>
  </div>
    <small>FOR DEV goalid: {{aNewsEntry.goalid}}</small>

