</main> -->
</template>

<script>
import { reactive, ref } from 'vue'
import axios from 'axios'
import { onMounted, toRef } from 'vue'

export default {
  props: ['aNewsEntry','entryType'],
  setup(props) {
    const aNewsEntry = toRef(props, 'aNewsEntry')
    const entryType = toRef(props, 'entryType')

    const state = reactive({
      aNewsEntry: aNewsEntry,
      entryType: entryType,
      goalDTO: ''
    });
  onMounted(() => { fetchGoalDTO() })

    const fetchGoalDTO = async () => {
      console.log("loginHandler launch")
      const url = "http://localhost:9090/api/getgoalbygoalid"

      const headers = {
        'Content-Type': 'application/json',
        Authorization: sessionStorage.getItem('TOKEN'),
        token: sessionStorage.getItem('TOKEN'),
      }
      //set cid
      const body = { goalid: state.aNewsEntry.goalid }
      await axios.post(url, body, { headers }).then(function (res) {
          state.goalDTO = res.data
      }).catch(() => {console.log("there was an error")})
    }

    return { state }
  }
}
</script>

<style scoped>

.card {
  border: solid black 1px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px;
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}
.card:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}
.content{
  width: 400px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}
.ellipsis {
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

</style>