<template>
<div class="card border-success mb-3" style="max-width: 30rem;">
  <div class="card-header content">{{aNewsEntry.name}}</div>
  <div class="card-body text-success">
    <p class="card-text ellipsis ">{{aNewsEntry.content}}</p>
    <h6 class="card-title" v-if="entryType=='newGoal'" >{{aNewsEntry.dueDate.substring(5)}} 까지</h6>
    <h6 class="card-title" v-else>on&nbsp;{{aNewsEntry.dueDate.substring(5)}}</h6>
    <h6 class="card-title" v-else>상태 : {{aNewsEntry.qdone}}/{{aNewsEntry.successQ}}{{aNewsEntry.unit}}</h6>
    <!-- <h7 class="card-title" >regDate: {{formatDate(aNewsEntry.regDate)}}</h7> -->
    <!-- <h7 class="card-title" >FOR DEV goalid: {{aNewsEntry.goalid}}</h7> -->
    <!-- <h7 class="card-title" >qdone 4DEV:{{aNewsEntry.qdone}}</h7> -->

  </div>
</div>

<!-- <main class="card">
  
  <section >
    <h4>{{aNewsEntry.name}}</h4>
    not accurate but works...
    <div class="low-line-gap">
    <p>Details : {{aNewsEntry.content}}</p>
    <p v-if="entryType=='newGoal'" >Done by: {{aNewsEntry.dueDate}}</p>
    <p v-else>Was completed : {{aNewsEntry.dueDate}}</p>
