</template>

<script>
import { reactive, ref } from 'vue'
import { onMounted, toRef } from 'vue'
import dayjs from 'dayjs'
export default {
  props: ['aNewsEntry','entryType'],
  setup(props) {
    const aNewsEntry = toRef(props, 'aNewsEntry')
    const entryType = toRef(props, 'entryType')
    const formatDate = (dateString) => {
          const date = dayjs(dateString);
          return date.format('YY년 MM월 D일');
    }
    const state = reactive({
      aNewsEntry: aNewsEntry,
      entryType: entryType
    });

    return { state,formatDate }
  }
}
</script>
<style scoped>
.card {
  border: solid black 1px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px;
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}
.card:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}
.content{
  width: 400px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}
.ellipsis {
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

</style>