<template>
  <h3 style="font-weight:900;">뉴스피드</h3>
  <hr />
  <div style="overflow-y: scroll;height: 600px;" data-bs-spy="scroll" data-bs-target="navbar-example2" tabindex="0">
    <div data-bs-spy="scroll" data-bs-target="#navbar-example2" data-bs-root-margin="0px 0px -40%"
      data-bs-smooth-scroll="true" class="scrollspy-example bg-light p-3 rounded-2" tabindex="0">
      <h5 id="scrollspyHeading1">회원들의 새로운 목표</h5>
      <p>
      <section v-for="(aNewsEntry) in state.newestRegdateGoalDTO">
        <TheNewsFeedGoalEntry :entryType="'newGoal'" :aNewsEntry="aNewsEntry" />
      </section>
      </p>

      <h5 id="scrollspyHeading2">회원들이 최근에 성취한 목표</h5>
      <p>
      <section v-for="(aNewsEntry) in state.lastDoneGoalDTO">
        <TheNewsFeedGoalEntry :aNewsEntry="aNewsEntry" />
      </section>
      </p>

      <h5 id="scrollspyHeading3">회원들이 최근에 완료한 일</h5>
      <p>
      <section v-for="(aNewsEntry) in state.lastDoneCalendarDTO">
        <TheNewsFeedCalendarEntry :aNewsEntry="aNewsEntry" />
      </section>
      </p>

      <h5 id="scrollspyHeading4">우리 그룹의 새 회원</h5>
      <p>
      <section v-for="(aNewsEntry) in state.newestCircleMemberDTO">
        <TheNewsFeedMemberEntry :aNewsEntry="aNewsEntry" />
      </section>
      </p>

      <h5 id="scrollspyHeading5">새로 올라온 질문</h5>
      <p>
      <section v-for="(aNewsEntry) in state.xNewestQuestionDTO">
        <TheNewsFeedQuestionEntry :aNewsEntry="aNewsEntry" />
      </section>
      </p>
    </div>
  </div>

</template>
<script>
import { reactive, ref } from 'vue'
import { onMounted, toRef } from 'vue'
import NewsEntry from './TheNewsFeedEntry.vue';
import TheNewsFeedGoalEntry from './TheNewsFeedGoalEntry.vue';
import TheNewsFeedCalendarEntry from './TheNewsFeedCalendarEntry.vue';
import TheNewsFeedMemberEntry from './TheNewsFeedMemberEntry.vue';
import TheNewsFeedQuestionEntry from './TheNewsFeedQuestionEntry.vue';
import axios from 'axios'

export default {
  props: {
    circleid: Number,
  },
  components: { NewsEntry, TheNewsFeedGoalEntry, TheNewsFeedCalendarEntry, TheNewsFeedMemberEntry, TheNewsFeedQuestionEntry },
  setup(props) {
    onMounted(() => { fetchNews() })
    const cid = toRef(props, 'circleid')
    const state = reactive({
      cid: cid,
      lastDoneGoalDTO: '',
      newestRegdateGoalDTO: '',
      lastDoneCalendarDTO: '',
      newestCircleMemberDTO: '',
      xNewestQuestionDTO: '',
    });
    const fetchNews = async () => {
      const url = "http://localhost:9090/api/newsfeed"

      const headers = {
        //TODO send the right amound of tokent and receive the same...
        'Content-Type': 'application/json',
        Authorization: sessionStorage.getItem('TOKEN'),
        token: sessionStorage.getItem('TOKEN'),
      }
      //set cid
      const body = { cid: state.cid }
      await axios.post(url, body, { headers }).then(function (res) {
        state.lastDoneGoalDTO = res.data.lastDoneGoalDTO
        state.newestRegdateGoalDTO = res.data.newestRegdateGoalDTO
        state.lastDoneCalendarDTO = res.data.lastDoneCalendarDTO
        state.newestCircleMemberDTO = res.data.newestCircleMemberDTO
        state.xNewestQuestionDTO = res.data.xNewestQuestionDTO
      }).catch(() => { console.log("there was an error") })
    }
    return { state, fetchNews }
  }
}
</script>

<style scoped>
.newsFeed {
  display: grid;
  /* margin: 3em 30em; */
  grid-template-columns: auto;
  gap: 10px;
  /* background-color: rgb(178, 182, 173); */
}

h5 {
  color: rgb(28, 16, 11)
}

html {
  scroll-behavior: smooth;
}
</style>